*{
  box-sizing: border-box;
}
html,body,#root{
  margin:0;
  padding:0;
  height:100%;
}
body {
  margin: 0;
  font-family: 'Jetbrains',monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.icon::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}
